"use client";

import React, {useState} from 'react'
import {AuthButton, FormLine} from "@/components/FormLine";

const SignInPage = () => {
    const [username, setUsername] = useState('');
    const [password, setPassword] = useState('');
    const [email, setEmail] = useState('');
    const [isSignUp, setIsSignUp] = useState(false);

    const handleSubmit = (e: any) => {
        e.preventDefault();
        console.log({username, password});
    }
    const userIcon = '/userIcon.png';
    const passwordIcon = '/passwordIcon.png';
    const emailIcon = '/emailIcon.png';

    return (
        <div className="flex-center flex-1 w-screen h-screen">
            <div className="flex flex-row w-2/3 h-2/3 rounded-lg border-2 border-dark-400">

                {/*Blue Sliding Panel*/}
                <div className={`flex flex-col justify-center w-1/2 h-full bg-dark-400 rounded-md transition-transform duration-700 ease-in-out ${isSignUp ? "translate-x-0 left-0" : "translate-x-full right-0"}`}>
                    <div className="w-1/2 mx-10">
                        <h2 className="text-right">WELCOME BACK!</h2>
                        <p className="text-right">We are happy to have you with this again</p>
                    </div>
                </div>

                {/*Sign in Forms Section*/}
                <div className={`flex-col w-1/2 h-full flex-center space-y-10 transition-opacity duration-700 left-0 ${
                    isSignUp ? "opacity-0 pointer-events-none" : "opacity-100"}`}>

                    <h2 className="text-4xl">Sign in</h2>

                    <form onSubmit={handleSubmit} className="space-y-8 w-2/3">
                        {/*Username */}
                        <FormLine
                            placeholder="Username"
                            value={username}
                            onChangeFunc={(e) => setUsername(e.target.value)}
                            icon={userIcon}
                        />

                        {/*Password*/}
                        <FormLine
                            placeholder="Password"
                            value={password}
                            onChangeFunc={(e) => setPassword(e.target.value)}
                            icon={passwordIcon}
                        />

                        {/*Button*/}
                        <AuthButton
                            authChoice="Sign in"
                        />
                    </form>

                    <div className="flex-center flex-col space-y-2">
                        <p className="text-sm">Don't have an account?</p>
                        <a href="/sign-up" className="text-dark-400 hover:underline font-bold text-sm">Sign up</a>
                    </div>
                </div>

                {/*Sign up Form Section*/}
                <div className={`flex-col w-1/2 h-full flex-center space-y-10 transition-opacity duration-700 left-1/2 ${
                    isSignUp ? "opacity-100 pointer-events-none" : "opacity-0"}`}>

                    <h2 className="text-4xl">Sign up</h2>

                    <form onSubmit={handleSubmit} className="space-y-8 w-2/3">
                        {/*Username */}
                        <FormLine
                            placeholder="Username"
                            value={username}
                            onChangeFunc={(e) => setUsername(e.target.value)}
                            icon={userIcon}
                        />

                        {/*Email*/}
                        <FormLine
                            placeholder="Email"
                            value={email}
                            onChangeFunc={(e) => setEmail(e.target.value)}
                            icon={emailIcon}
                        />

                        {/*Password*/}
                        <FormLine
                            placeholder="Password"
                            value={password}
                            onChangeFunc={(e) => setPassword(e.target.value)}
                            icon={passwordIcon}
                        />

                        {/*Button*/}
                        <AuthButton
                            authChoice="Sign up"
                        />
                    </form>

                    <div className="flex-center flex-col space-y-2">
                        <p className="text-sm">Don't have an account?</p>
                        <a href="/sign-in" className="text-dark-400 hover:underline font-bold text-sm">Sign in</a>
                    </div>
                </div>
            </div>

        </div>
    )
}
export default SignInPage;


